<template>
	<ul class="data-list">
		<van-loading v-if="loading" type="spinner" size="24"/>
		<template v-else>
			<div v-if="nodata" class="no-data"></div>
			<template v-else>
				<li v-for="(item, i) in questions" :key="i" @click="onClick(item)">
					<div class="item-img">
						<van-image :src="item.icon">
							<template v-slot:loading>
								<van-loading type="spinner" size="20" />
							</template>
						</van-image>
					</div>
					<div class="item-content">
						<div class="title">{{item.estimateName}}</div>
						<div class="more">
							<span v-if="item.estimateCode*1===2">评估状态：<span style="color: red;">已评估</span></span>
							<span v-else>评估状态：<span style="color: #1aad19;">未评估</span></span>
						</div>
						<div class="time">活动时间：{{formatDate(item.beginTime)}}~{{formatDate(item.endTime)}}</div>
					</div>
					<van-icon name="arrow" />
				</li>
			</template>
		</template>
	</ul>
</template>

<script>
	import { mapState } from "vuex"
	const icon = require('../../assets/images/question.png')
	export default {
		name: 'questionlist',
		data() {
			return {
				loading: true,
				nodata: false,
				questions: []
			}
		},
		computed: {
			...mapState(['info'])
		},
		mounted() {
			this.getData()
		},
		methods: {
			formatDate(date) {
				date = this.$Utils.toDate(date)
				return this.$Utils.dateFtt('yyyy-MM-dd', date)
			},
			// 培训ID获取评估
			async getData() {
				let params = {
					studentId: this.info.id,
					cultivateId: this.$route.query.id
				}
				let res = await this.$Request.get('/estimate/getEstimateByWeChat.do', params)
				this.loading = false
				if(res && res.code === 0){
					let data = res.list || []
					this.nodata = !data.length
					data.forEach(item => { item.icon = icon })
					this.questions = data
				} else {
					this.nodata = true
				}
			},
			onClick(item) {
				this.$router.push({
					path: '/question',
					query: {
						id: this.$route.query.id,
						queid: item.id
					}
				})
			}
		}
	}
</script>

<style>

</style>


